import React, {Component} from 'react';
import './FormContainer.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Form, Button, Col, Container, Row, Modal} from 'react-bootstrap';
import 'react-datez/dist/css/react-datez.css';
import { ReactDatez } from 'react-datez';
import firebase from './firebase.js';
// import firebase from 'firebase/app';
import 'firebase/database';
import voters from './sample_voters.json';

class FormContainer extends Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
        firstName: '',
        middleInitial: '',
        lastName: '',
        dateInput: '',
        interest: '',
        show: false,
        curr_volunteer: '',
        rec: []
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleClose = this.handleClose.bind(this);
  }

  handleChange(value) {
      this.setState({ dateInput: value })
  }

  handleClose() {
  this.setState({ show: false });
}


  onChange = (e) => {
        /*
          Because we named the inputs to match their
          corresponding values in state, it's
          super easy to update the state
        */
        this.setState({ [e.target.name]: e.target.value });
      }

  onSubmit = (e) => {
          e.preventDefault();
          const volunteerRef = firebase.database().ref('volunteers');
          const volunteer = {
            name: this.state.firstName + ' ' + this.state.middleInitial + ' ' + this.state.lastName,
            age: 2019 - this.state.dateInput.substring(0,4),
            interest: this.state.interest,
            fname: this.state.firstName,
            lname: this.state.lastName,
            mi: this.state.middleInitial,
            dob: this.state.dateInput.substring(0,10)
          }
          volunteerRef.push(volunteer);
          this.setState({
            curr_volunteer : {name:this.state.firstName + ' ' + this.state.middleInitial + ' ' + this.state.lastName, age:2019 - this.state.dateInput.substring(0,4), interest:this.state.interest},
            firstName: '',
            middleInitial: '',
            lastName: '',
            dateInput: '',
            interest: '',
            show: true
          });
      }


  render() {
     const {rec, curr_volunteer, firstName, middleInitial, lastName, dateInput, interest } = this.state;
     if(curr_volunteer.name != null){
       var arr = voters;
       var isNotEmpty = false;
       // var rec = {};
       // console.log(arr[1].name);
       var counter = 0;
       for (var i = 0; i < arr.length; i++){
          var obj = arr[i];
          if(obj.age < curr_volunteer.age + 5 && obj.age > curr_volunteer.age - 5){
            counter = counter + 1;
            rec[counter] = {name: obj.name, number: obj.phone_num};
            console.log(rec[1]);
            isNotEmpty = true;
          }
        }
      }
    return (
      <div className="Form">
      <header className="form-style">


      <Modal show={this.state.show} onHide={this.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Next Steps...</Modal.Title>
          </Modal.Header>
          <Modal.Body> <h1> Hi {curr_volunteer.name}! Thank you so much for choosing to volunteer with us! </h1> {isNotEmpty ? <p> Based on your desire to {curr_volunteer.interest} we would recommend you get in touch with {rec[1].name} at {rec[1].number} </p> : <p> Unfortunately, we have no available tasks at this time, but keep checking back! </p>}

          </Modal.Body>
          <Modal.Footer>
            <Button variant="danger" onClick={this.handleClose}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>


      <Container>
        <Row>
          <Col>
            <h1>
            Volunteer for Karen
            </h1>
            <p>
            Creating change requires your engagement. In order to succeed, we need your help.
            </p>
            <p>
            Add your name now to volunteer for Karen Smith's campaign for Michigan's 8th Congressional District.
            </p>
          </Col>
          <Col>
          <Form onSubmit={this.onSubmit}>
            <Form.Row>
              <Col>
                <Form.Group controlId="formFirstName">
                  <Form.Label>First Name</Form.Label>
                  <Form.Control type="text" placeholder="First Name" name="firstName" value={firstName} onChange={this.onChange} />
                </Form.Group>
                </Col>
                <Col>
                <Form.Group controlId="formMiddleInitial">
                  <Form.Label>M.I.</Form.Label>
                  <Form.Control type="text" placeholder="M.I." name="middleInitial" value={middleInitial} onChange={this.onChange}/>
                </Form.Group>
                </Col>
                <Col>
                <Form.Group controlId="formLastName">
                  <Form.Label>Last Name</Form.Label>
                  <Form.Control type="text" placeholder="Last Name" name="lastName" value={lastName} onChange={this.onChange}/>
                </Form.Group>
                </Col>
              </Form.Row>

              <Form.Row>
                <Col>
                  <Form.Group controlId="exampleForm.ControlSelect1">
                    <Form.Label>Interests</Form.Label>
                    <Form.Control as="select" name="interest" value={interest} onChange={this.onChange}>
                      <option>I'd like to...</option>
                      <option>register voters</option>
                      <option>make phone calls</option>
                      <option>text volunteers</option>
                    </Form.Control>
                  </Form.Group>
                </Col>
                <Col>
                  <Form.Group>
                  <Form.Label htmlFor="exampleDate2">DOB</Form.Label>
                  <ReactDatez name="dateInput"
                  handleChange={this.handleChange}
                  value={dateInput}
                  allowPast= {true}
                  allowFuture= {false}
                  dateFromat="MM/DD/YYYY"
                  />
                  </Form.Group>
                </Col>
              </Form.Row>

              <Button variant="outline-danger" type="submit">
                Sign Up
              </Button>
            </Form>
          </Col>
        </Row>
      </Container>
        </header>
      </div>
    );
  }
  }

export default FormContainer;
